body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media (max-width: 576px){
  .control-panel {
      width: 100%;
  }
}


@media (max-width: 576px){
  .language-control-panel {
      width: 100%;
      position: absolute;
      right: 5px;
      top: 5px;
      -webkit-align-items: flex-end;
              align-items: flex-end;
  }
}

.btn {
  cursor: pointer;
}

.btn:hover {
  color: #08f;
}

.switch-wrapper {
  position: relative;
}
 
.switch-wrapper > div {
  position: absolute;
}

#setup_button {
  display: none;
}

.cluster-marker {
  color: #fff;
  background: #1978c8;
  border-radius: 50%;
  padding: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.checkin-marker {
  background: none;
  border: none;
}

.checkin-marker img {
  width: 25px;
}
